<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="150px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="分享模板">
              <el-upload
                :limit="1"
                :headers="myHeaders"
                :action="'/v1/api/api/api/upload'"
                :on-success="fileSuccess"
                :file-list="file"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽850px 高:1280px 格式:png、jpg、jpeg
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="背景图">
              <el-upload
                :limit="1"
                :headers="myHeaders"
                :action="'/v1/api/api/api/upload'"
                :on-success="bgfileSuccess"
                :file-list="bgfile"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽850px 高:1280px 格式:png、jpg、jpeg
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="是否开启分享审核">
              <el-switch
                v-model="share_sh"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text="打开审核"
                inactive-text="关闭审核"
                @change="shareHandle"
              >
              </el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <el-col>
        <el-form-item>
          <el-button type="primary" size="small" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      setting: {},
      file: [],
      bgfile: [],
      share_sh: true,
    }
  },
  mounted() {
    this.loading = true
    Fetch2('/sys/config/infojz')
      .then(r => {
        if (r.data.image) {
          this.file = [
            {
              name: r.data.image,
              url: r.data.image,
            },
          ]
        }
        if (r.data.bgimage) {
          this.bgfile = [
            {
              name: r.data.bgimage,
              url: r.data.bgimage,
            },
          ]
        }
        r.data.review == 1 ? (this.share_sh = true) : (this.share_sh = false)
        this.setting = r.data
        console.log('data', this.setting)
        this.loading = false
      })
      .catch(() => (this.loading = false))
  },
  computed: {},
  methods: {
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },

    onSubmit() {
      this.loading = true
      var sh = 2
      if (this.share_sh) {
        sh = 1
      } else {
        sh = 2
      }
      Fetch3('/sys/config/infojz', {
        ...this.setting,
        review: sh,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
    fileSuccess(res) {
      console.log('res->', res)
      this.setting.image = res.data.file
    },
    bgfileSuccess(res) {
      console.log('resbg->', res)
      this.setting.bgimage = res.data.file
    },
    shareHandle(res) {
      if (res) {
        this.share_sh = true
      } else {
        this.share_sh = false
      }
    },
  },
}
</script>
<style lang="less" scoped>
.setting {
  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
